@import "~antd/dist/antd.less";

@header: 64px;
@duo: 130px;
@font-face {
  font-family: "Michroma";
  src: url("./static/Michroma/Michroma-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Chakra Petch";
  src: url("./static/Chakra_Petch/ChakraPetch-Medium.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

.App {
  // height: 100vh;
  // width: 100vw;
  // overflow-x: hidden;
  // overflow-y: scroll;
  // overflow: scroll;
  background: rgba(22, 35, 42, 1);

  @media only screen and (min-width: 822px) {
    .AppBox {
      height: 100vh;
      width: 100vw;
      overflow: scroll;
      background: url("./static/iii.png") no-repeat top right;
      background-size: 768px auto;
    }
    .mobilHeader {
      display: none;
    }
    .header {
      height: 96px;
      // display: inline-block;
      max-width: 1200px;
      margin: 0 auto;
      padding: 30px 0;
      .box {
        display: flex;
        justify-content: space-between;
        align-self: center;
      }
      .right {
        ul {
          li {
            display: inline-block;
            a {
              &.active {
                color: #07b169;
              }
              width: 63px;
              height: 20px;
              padding: 0 20px;
              /* body/body */
              font-family: "Chakra Petch";

              font-weight: 400;
              font-size: 16px;
              line-height: 20px;
              /* identical to box height, or 125% */

              color: #7f8790;
              &:hover {
                color: #07b169;
              }
            }
          }
        }
      }
      .left {
        img {
          /* logo/color-on-dark */

          width: 64px;
          height: 27.5px;

          /* Inside auto layout */
        }
      }
    }
    .bottomBox {
      /* footer */

      box-sizing: border-box;
      border-top: 1px solid #1f2d34;
      // padding: 20px 0;
      // height: 84px;
      .texbox {
        max-width: 1200px;
        margin: 0 auto;
        /* Copyright 2024 stcubed */

        // height: 20px;
        height: 84px;
        line-height: 84px;

        /* body/body */
        font-family: "Chakra Petch";
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        // line-height: 20px;
        /* identical to box height, or 125% */

        color: #7f8790;

        /* Inside auto layout */
        flex: none;
        order: 0;
        flex-grow: 1;
      }
    }
    .bgList {
      display: none;
    }
  }
  @media only screen and (min-width: 100px) and (max-width: 821px) {
    .AppBox {
      // height: 100vh;
      width: 100vw;
      overflow: scroll;
      // overflow: scroll-y;
      // background: url("./static/iii.png") no-repeat top right;
      // background-size: 768px auto;
      > .header {
        display: none;
        padding: 30px 0;
      }
    }

    .bottomBox {
      /* footer */

      box-sizing: border-box;
      border-top: 1px solid #1f2d34;
      // padding: 20px 0;
      // height: 84px;
      .texbox {
        // max-width: 1200px;
        margin: 0 auto;
        /* Copyright 2024 stcubed */

        // height: 20px;
        height: 84px;
        line-height: 84px;

        /* body/body */
        font-family: "Chakra Petch";
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        // line-height: 20px;
        /* identical to box height, or 125% */

        color: #7f8790;
        padding: 0 30px;
        /* Inside auto layout */
        flex: none;
        order: 0;
        flex-grow: 1;
      }
    }
    .mobilHeader {
      display: block !important;
      height: 96px;
      .box {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 30px 40px;
        a {
          img {
            width: 64px;
            height: auto;
          }
        }
        .right {
          font-family: "Chakra Petch";
          font-weight: 400;
          font-size: 16px;
          // height: 28px;
          line-height: 20px;
          color: #7f8790;
        }
      }
    }
    .bgList {
      display: block;
      height: 100vh;
      position: fixed;
      z-index: 1;
      /* mobile */
      left: 0;
      top: 0;
      width: 100%;
      background: rgba(0, 0, 0, 0.64);
      // opacity: 0.6;
      .container {
        padding: 20px 30px;
        background-color: rgba(22, 35, 42, 1);
        width: 100%;
        height: 365px;
        .icon {
          text-align: right;
          img {
            width: 1rem;
            height: auto;
          }
        }
        .ulBox {
          padding-top: 2rem;
          div {
            text-align: right;
            padding: 15px 0;
            a {
              &.active {
                color: #07b169;
              }
              width: 63px;
              height: 20px;
              padding: 0 20px;
              /* body/body */
              font-family: "Chakra Petch";

              font-weight: 400;
              font-size: 16px;
              line-height: 20px;
              /* identical to box height, or 125% */

              color: #7f8790;
              &:hover {
                color: #07b169;
              }
            }
          }
        }
      }
    }
  }
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.paddingBottom {
  padding-bottom: 20px;
}
.App-header {
  // background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.tablePagination {
  text-align: right;
  padding: 20px 0;
}
.homeindexcontainer {
  .ant-page-header-heading {
    .ant-page-header-heading-left {
      .ant-page-header-heading-title {
        padding-left: 25px;
      }
    }
  }
}

.Breadcrumbbox {
  display: inline-block;
  padding-bottom: 15px;
}
.ant-page-header {
  padding: 16px 0;
}

.site-layout1111 {
  .pagecontainer {
    padding: 20px;
  }

  .father1 {
    position: relative;
    z-index: 1;
    box-shadow: 0px 5px 13px 0px rgba(78, 89, 105, 0.06);
  }

  .buheader {
    height: @header;
    background-color: #467dfd;
    .systeambox {
      display: inline-block;
      padding-left: 20px;

      .systemTitle {
        display: inline-block;
        height: @header;
        line-height: @header;
        font-size: 24px;
        font-family: PingFangSC-Medium, PingFang SC;
        font-weight: 500;
        color: #ffffff;
      }
    }

    .tipnpx {
      height: @header;
      display: flex;
      justify-content: center;
      align-items: center;
      // width: 440px;
      padding-right: 20px;
      display: inline-block;

      p {
        display: inline-block;
        width: 380;
        // height: @header;
        // line-height: @header;
        font-size: 17px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: #c0c4cc;
        padding: 0 10px;

        // overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }

    .userinfo {
      cursor: pointer;
      float: right;
      height: @header;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-right: 20px;
      .linebox {
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
      .span1 {
        font-size: 20px;
        padding: 0 20px;
        color: #fff;
      }
    }

    span {
      font-size: 26px;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: 500;
      color: #ffffff;
      line-height: @header;
    }
  }

  .ant-layout-header {
    background: #f5f5f5;
    background-size: 100% 100%;
    height: 55px;
  }

  .ant-layout-sider-children {
    .ant-menu {
      .ant-menu-item-selected {
        background-color: #f2f3f5;

        .ant-menu-title-content {
          color: #165dff;
        }

        .ant-menu-item-icon {
          color: #165dff;
        }
      }
    }
  }

  .site-layout-background {
    // background: #EBEEF0;
    height: calc(100vh - @duo);
    overflow-y: auto;
    overflow-x: hidden;

    > div {
      height: 100%;
    }
  }
}

.formbottompdbottom0 {
  .ant-modal-body {
    padding-bottom: 0;
  }
}

@primary-color: #467DFD;